import React, { PureComponent } from "react";
import DataContext from "../data-context";
import './style.css'

class VideoPage extends PureComponent {
  static contextType = DataContext;

  render() {
    const video = this.context.videos.find(
      vid => vid.id === this.props.match.params.id
    );
    if (!video) {
      return <h2> NO SUCH VIDEO </h2>;
    }

    return (
      // eslint-disable-next-line jsx-a11y/iframe-has-title
      <iframe
        className="video"
        src="http://www.youtube.com/embed/6xisazZX9bA"
        frameborder="0"
        allowfullscreen
      ></iframe>
    );
  }
}

export default VideoPage;
